.grid {
	--grid-gap: 0px;
	display: flex;
	flex-wrap: wrap;
}
.grid > * {
	flex-basis: 100%;
}

[class*=grid-gap] {
	margin-bottom: calc(var(--grid-gap, 1em) * -1);
	margin-left: calc(var(--grid-gap, 1em) * -1);
}
[class*=grid-gap] > * {
	margin-bottom: var(--grid-gap, 1em);
	margin-left: var(--grid-gap, 1em);
}

