.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	word-wrap: normal;
	border: none;
}
.screen-reader-text:active, .screen-reader-text:focus {
	clip: auto;
	clip-path: none;
	display: block;
	height: auto;
	width: auto;
	z-index: 100000;
	text-decoration: none;
}

.skip-link {
	position: absolute;
	z-index: var(--zindex-fixed-element);
	top: 0;
	left: 0;
	display: inline-block;
	padding: var(--space-sm) var(--space-md);
	background-color: var(--color-bg);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
}
.skip-link:focus {
	clip: auto;
	clip-path: none;
	--color-outline: hsla(var(--color-primary-h), var(--color-primary-s), var(--color-primary-l), 0.2);
	outline: 3px solid var(--color-outline);
}

@media (min-width: 48rem) {
	.alignleft,
.alignright {
		max-width: 35%;
	}
}
@media (min-width: 64rem) {
	.alignleft,
.alignright {
		max-width: 50%;
	}
}

@media (min-width: 48rem) {
	.alignleft {
		float: left;
		margin: 0 var(--component-padding) var(--component-padding) 0;
	}
}

@media (min-width: 48rem) {
	.alignright {
		float: right;
		margin: 0 0 var(--component-padding) var(--component-padding);
	}
}

.aligncenter {
	display: block;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
	clear: both;
}

.alignfull,
.alignwide {
	margin-left: calc( -100vw / 2 + 100% / 2);
	margin-right: calc( -100vw / 2 + 100% / 2);
	width: auto;
	max-width: 100vw;
}

@media (min-width: 64rem) {
	.alignwide {
		max-width: calc(var(--max-width-sm) + 50%);
		width: 100vw;
		margin-left: 50%;
		transform: translateX(-50%);
	}
}
